.card {
  padding: 24px;
  display: flex;
  justify-content: flex-start;
  border-radius: 4px;
  border: 1px solid #ced4da;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #fff;
}

.symbol {
  font-size: 48px;
  margin-bottom: 18px;
}

.title {
  font-weight: 700;
  line-height: 28px;
  font-size: 17px;
  margin-bottom: 12px;
  text-decoration: underline;
}

.keywords {
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
}
